body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
    --bg-dark: #1a2f3a;
    --logo-height: 100px;
    --header-height: 100px;
    --aside-width: 225px;
    --footer-height: 40px;
    --shadow: 0 2px 23px 0 rgba(0, 0, 0, 0.1), 0 2px 49px 0 rgba(0, 0, 0, 0, 0.06);
}

*{
    box-sizing: border-box;
    font-family: 'Montserrat',sans-serif;
}

.app{
    margin: 0;
    display: grid;
    grid-template-columns: 225px 1fr;
    grid-template-columns: var(--aside-width) 1fr;
    grid-template-rows: 100px 1fr 40px;
    grid-template-rows: var(--header-height) 1fr var(--footer-height);
    grid-template-areas: "logo header" "menu content" "menu footer";
    height: 100vh;
    background-color: #f5f5f5;

}

aside.logo{
    grid-area: logo;
}
aside.menu{
    grid-area: menu;
}
header.header{
    grid-area: header;
}

main.content{
    grid-area: content;
}

footer.footer{
    grid-area: footer;
}
main > div {
    background-color: #fff;
}
header.header{
    background-color: #fff;
    padding: 0px 15px;
    overflow: hidden;
    white-space: nowrap;
    box-shadow: var(--shadow);
}

header.header h1{
    font-size: 1.8em;
}
aside.logo{
    background: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
}

aside.logo img{
    padding: 0px 15px;
    width: 100%;
}
aside.menu-area {
    background-color: var(--bg-dark);
    box-shadow: 2px 0 10px 0 rgba(0, 0, 0, 0.12), 2px 0 15px 0 rgba(0, 0, 0, 0.09);
}

.menu a{
    display: block;
    text-decoration: none; 
    color: #fff;
    font-weight: 300;
    padding: 15px;
}

.menu a:hover{
    background: #333;
}
footer.footer{
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding: 0 25px;
    background-color: #fff;
    box-shadow: var(--shadow);
}
