aside.menu-area {
    background-color: var(--bg-dark);
    box-shadow: 2px 0 10px 0 rgba(0, 0, 0, 0.12), 2px 0 15px 0 rgba(0, 0, 0, 0.09);
}

.menu a{
    display: block;
    text-decoration: none; 
    color: #fff;
    font-weight: 300;
    padding: 15px;
}

.menu a:hover{
    background: #333;
}